@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400&display=swap);
:root {
  font-size: 62.5%
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {

    --darkest-mint: #17A744;
    --dark-mint: #21E15D;
    --main-mint: #4EE77E;
    --light-mint: #6CEB94;
    --lightest-mint: #a8f3bf;

    --darkest-blue: #0088A0;
    --dark-blue: #00B3D3;
    --main-blue: #18DCFF;
    --light-blue: #39F8F5;
    --lightest-blue: #A0F1FF;

    --darkest-accent: #FE7C00;
    --dark-accent: #FF8E21;
    --main-accent: #FF9F43;
    --light-accent: #FFB065;
    --lightest-accent: #ffc287;

    --main-red: #FF898A;

    --main-white: #fff;
    --main-black: #161616;
    --main-gray: #e6e6e6;
    --alt-gray: #9b9b9b;
    --main-transparent: #ffffffa5;
    --alt-transparent: #5c5c5ca5;

    --alt-shadow: #363636cc;
    --main-shadow: #36363650;
}

::-webkit-scrollbar {
    width: 7px;
    background-color: none;
}

::-webkit-scrollbar-thumb {
    background-color: #6CEB94;
    background-color: var(--light-mint);
    border-radius: 10px;
    box-shadow: inset 0 0 3px #363636cc;
    box-shadow: inset 0 0 3px var(--alt-shadow);
}

.main-container-style {
    display: flex;
    height: 80vh;
    margin: auto;
    padding: 1em
}

.child-container-style {
    display: grid;
    grid-template-rows: 7.5% auto;
    padding: .5em;
    font-family: 'Poppins', sans-serif;
    background-color: #ffffffa5;
    background-color: var(--main-transparent);
    border: 1px solid #00B3D3;
    border: 1px solid var(--dark-blue);
    border-radius: 15px;
    box-shadow: 2px 4px 8px #36363650;
    box-shadow: 2px 4px 8px var(--main-shadow);
    overflow-y: auto;
}

.container-inner-title {
    align-self: center;
    width: 95%;
    margin: .2em auto;
    padding-bottom: .2em;
    font-size: 1.8em;
    letter-spacing: .1em;
}

.d-grid-three-colums {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 3em;
}

.fs-medium {
    font-size: 1.2em;
}

.txt-bold {
    font-weight: 700;
}

.align-mid {
    text-align: center;
}

.align-left {
    text-align: left;
}

.align-right {
    text-align: right;
}

.text-border-bottom {
    border-bottom: 1px solid #161616;
    border-bottom: 1px solid var(--main-black);
}

.alt-text-border-bottom {
    border-bottom: 1px solid #5c5c5ca5;
    border-bottom: 1px solid var(--alt-transparent);
}

.main-blue {
    color: #18DCFF;
    color: var(--main-blue);
}

.main-mint {
    color: #4EE77E;
    color: var(--main-mint);
}

.bg-mint {
    background-color: #a8f3bf;
    background-color: var(--lightest-mint);
}

.bg-blue {
    background-color: #A0F1FF;
    background-color: var(--lightest-blue);
}

.bg-accent {
    background-color: #ffc287;
    background-color: var(--lightest-accent);
}
.new-user-form,
.fieldset {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.new-user-form {
    height: 80vh;
    width: 40%;
    margin: auto;
    overflow-y: auto;
}

.fieldset {
    justify-content: center;
    width: 100%;
    border: 2px solid var(--darkest-blue);
    border-radius: 1em;
    background-color: var(--main-transparent);
}

.fieldset-legend {
    font-size: 1.4em;
}

.new-user-input{
    min-width: 200px;
    margin-bottom: .6em;
    padding: .5em;

    font-weight: 700;
    text-align: center;
    border: .5px solid var(--darkest-blue);
    border-radius: 1em;
}

.new-user-input:focus {
    outline: none;
    border: 1px solid var(--main-accent);
}

.new-user-button {

    height: 2em;
    width: 20%;
    margin: .3em;

    color: var(--main-white);
    font-size: 1.4em;
    font-weight: 700;
    letter-spacing: 0.02em;

    border: 3px solid var(--darkest-blue);
    border-radius: 1em;
    background-color: var(--darkest-blue);
    box-shadow: 0px 0px 4px var(--main-white);
}

.new-user-button:hover {
    box-shadow: 0px 0px 8px var(--main-accent);
}

.new-user-button:focus {
    border: 2px solid var(--main-accent);
    outline: none;
}

.error {

}

.header {
    height: 10vh;
    background-color: var(--darkest-mint);

    display:flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.header-link {
    height: 100%;

    display: flex;
    justify-content: center;
    text-decoration: none;
}

.header-logo {
    display: flex;
    flex-grow: 2;
    text-decoration: none;
    justify-content: center;
}

.header-nav-element{
    width: 0;
    
    color: #E7F5E7;

    font-weight: 500;
    align-items: center;
    font-size: 2.8em;
    flex-grow: 1;
}

.header-nav-left {
    border-right: solid 3px #148a39;
}

.header-nav-right {
    border-left: solid 3px #148a39;
}

.header-nav-element:hover {
    background-color: #b3d6b3;
}

.header-title {

    margin: 0.2em .1em;

    font-size: 6em;
    font-family: 'Righteous', cursive;

    color: transparent;
    -webkit-text-stroke-width: .5px;
    -webkit-text-stroke-color: white;

    transition: all .2s ease-in-out;
}

.header-title:hover {
    color: var(--main-mint);
}

.header-icon {
    width: 90px; 
    height: 75px;
}

.header-logout {
    text-align: end;
}

@media (max-width:500px) {
    .header-title{
        color: red;
        font-size: 4em;
        margin: 0 25px;
        align-items: center;
    }
}
.background {
    position: absolute;
    top: 0%;
    height: 100vh;
    width: 100%;
    z-index: -10;
    overflow: hidden;
}

.background-image {
    position: relative;
    top: 45%;
    left: -15%;
    
}
.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: absolute;
    bottom: 0;
    height: 10vh;
    width: 100%;

    font-family: 'Poppins', sans-serif;
    font-weight: 200;
    letter-spacing: .1em;

    color: var(--lightest-mint);
    background-color: var(--darkest-mint);

    overflow: hidden;
}

.created-by {
    margin: 0.2em 1em;
    font-size: 1.6em;
}

.author-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.author {
    margin: 0em 1em;
    font-size: 1.8em;
    color: var(--lightest-mint);
    text-decoration: none;
    transition: all .2s ease-in-out;
    border-bottom: 1px solid transparent;
}

.author:hover {
    letter-spacing: 0.11em;
    border-bottom: 1px solid var(--lightest-mint);
}

.author-paragraph {
    margin: 0;
}
.splash-page {
    display: flex;
    flex-direction: column;
    align-items: center;

    height: 78vh;
    width: 100%;

    padding: 1%;
    overflow: hidden;
}

.splash-greeting {
    height: 10%;
    margin-bottom: auto;

    font-family: 'Poppins', sans-serif;
    font-size: 4em;
    font-weight: 200;
    letter-spacing: .1em;;
    color: var(--darkest-mint);
}

.splash-content-area {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: 1fr 1fr;
    grid-column-gap: 2.5em;
    grid-row-gap: 1em;
    height: 90%;
    width: 80%;
}

.splash-content-container {
    display: grid;
    grid-template-rows: 15% auto 10%;

    padding: .5em;

    font-family: 'Poppins', sans-serif;

    background-color: var(--main-white);
    border: 1px solid var(--dark-blue);
    border-radius: 15px;
    box-shadow: 2px 4px 8px var(--main-shadow);
    overflow-y: auto;
}

.inbox {
    grid-column: 1/4;
}

.appointments-splash {
    grid-column: 4/10;
}

.appointment-splash {
    width: 98%;

    margin: 1em auto;
    padding: 1em;

    border-top: 2px solid var(--darkest-blue);
    border-bottom: 2px solid var(--darkest-blue);
    background-color: var(--main-gray);
}

.appointment-splash-title {
    font-size: 1.2em;
    letter-spacing: 0.05em;
    font-weight: 700;
}

.appointment-splash-date {
    color: var(--alt-shadow);
}

.appointment-splash-description {
    text-transform: capitalize;
    font-size: 1.3em;
}

.recommendations {
    grid-column: 1/5;
    grid-row-start: 2;

    display: grid;
    grid-template-rows: 15% auto;
}

.schedule {
    grid-column: 5/10;
    grid-row-start: 2;

    display: grid;
    grid-template-rows: 15% auto;
}

.schedule-title {
    font-size: 1.2em;
    letter-spacing: 0.05em;
    font-weight: 700;
}

.inbox-message-area,
.all-recommendations,
.appointments-entries-area
{
    width: 90%;
    margin: 1em auto;
    padding-right: 1em;
    overflow-y: auto;
}

.message-container {
    display: flex;
    flex-direction: column;
}

.message-icon-and-contact {
    display: flex;
    justify-content: space-between;
}

.contact {
    display: inline-block;
    text-transform: capitalize;
    font-size: 1.2em;
    color: var(--alt-shadow);
}

.message-text-container {
    padding: .8em;

    font-size: 1.2em;
    font-weight: 300;
    letter-spacing: .02em;
    border: 1px solid var(--dark-blue);
    border-radius: 5px;
    
    transition: all .2s ease-in-out;
}

.message-text-container:hover {
    box-shadow: 0px 2px 8px var(--main-shadow);
}



.recommendation {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin: 1em auto;
    padding: .5em;

    font-size: 1.4em;
    border: 1px solid var(--dark-blue);
    border-radius: 5px;
}

.splash-content-link {
    justify-self: center;

    font-size: 1.6em;
    letter-spacing: .08em;
    font-weight: 600;
    color: var(--dark-blue);
    transition: all .2s ease-in-out;
}

.inner-splash-content-link {
    margin-top: .4em;

    font-size: 1.2em;
    text-decoration: none;
    letter-spacing: .08em;
    font-weight: 600;
    color: var(--dark-blue);
    transition: all .2s ease-in-out;
}

.splash-content-link:hover,
.inner-splash-content-link:hover {
    letter-spacing: .1em;
}
button {
    transition: all 0.2s;
}

.activity {
    display: grid;
    grid-template-columns: [first-col] 20% [second-col] 30% [third-col] 30% [fourth-col];
    grid-template-rows: [first-row] auto [second-row] auto [third-row] auto [fourth-row];
    
    overflow: hidden;
    
    font-size: 2em;

    box-shadow: 2px 2px 4px #929292;
    background-color: #E7F5E7;
}

.activities-container {
    max-height: 80vh;
    display: flex;
    flex-direction: column;
    overflow: auto;
}

.activity-label {
    transition: all 0.2s;
    order: 0;

    padding: 10px 10px 15px 10px;
    margin: 20px 20% 10px 20%;

    background: #f8f8f8;

    border-top-right-radius: 0.4em;
    border-bottom-right-radius: 0.4em;
    border-left: solid 8px transparent;
}

.activity-label.done {
    text-decoration: line-through;
    order: 1;
}

.activity-label:hover {
    border-left: solid 8px #00B3D3;
}

.activity-label.done:hover {
    border-left: solid 8px crimson;
}

.activity-date {
    font-size: 2.3em;
    font-weight: 500;
    margin: 0 0 10px 10px;
}

.activity-text {
    grid-column-start: second-col;
    grid-column-end: fourth-col;
    grid-row-start: first-row;
    grid-row-end: second-row;

    margin: 10px 0 0 10px;

    font-size: 1.2em;
    font-weight: 500;
}

.description-text {
    grid-column-start: second-col;
    grid-column-end: fourth-col;
    grid-row-start: second-row;
    grid-row-end: third-row;
    
    padding: 10px 0 10px 10px;
}

.time-text {
    grid-column-start: first-col;
    grid-column-end: second-col;
    grid-row-start: first-row;
    grid-row-end: fourth-row;

    display: flex;
    justify-content: center;
    align-items: center;

    font-weight: 500;
    font-size: 1.5em;
    
    background-color: #9ee89e;
}

.button-container {
    grid-column-start: fourth-col;
    grid-row-start: first-row;
    grid-row-end: fourth-row;
}

.buttons {
    height: 30px;
    width: 120px;

    float: right;
    cursor: pointer;

    margin: 10px 20px 10px 0;
    border: none;
    border-radius: 6px;
}

.buttons:first-of-type {
    background-color: #A8F3BF;
}
.buttons:last-of-type {
    background-color: #FFCECF;
}

.buttons:first-of-type:hover {
    background-color: #4EE77E;
    color: #E7F5E7;
}

.buttons:last-of-type:hover {
    background-color: #FF5C5D;
    color: #E7F5E7;
}

.add-activity-button {
    top: 110px;
    left: 20px;
    position: absolute;

    padding: 12px;
    border-radius: 4px;
    font-size: 33px;

    color: white;
    background-color: #00B3D3;

}

.add-activity-button:hover {
    background-color: #0088A0;
}

.add-window-container {
    position: absolute;
    z-index: 10;

    width: 450px;
    height: 280px;

    font-family: 'poppins';

    border-radius: 5px;
    box-shadow: 1px 1px 4px #3c3c3c;
    background-color: #E7F5E7;
}

.add-window-bar {
    height: 30px;
    width: 100%;

    cursor: move;
    overflow: hidden;

    font-size: 2em;
    text-align: center;
    background-color: #9ee89e;
}

.add-window-close-btn {
    top: 0;
    right: 0;
    position: absolute;

    width: 30px;
    height: 30px;

    border: none;
    border-radius: 0;
    color: #1B1919;
    background-color: transparent;
}
.add-window-close-btn:hover {
    background-color: #FF898A;
}
.schedule-title {
 font-size: 2em;
}

.activityPreview {
    display: flex;

    margin: 1em auto;
    padding: 0.5em;

    background-color: var(--main-white);
    border: 1px solid var(--dark-blue);

    font-family: 'Poppins', sans-serif;
    font-size: 1.4em;

    flex-direction: column;
    border-radius: 5px;
    align-items: center;
}

.all-schedules{
    width: 90%;
    margin: 1em auto;
    overflow-y: auto;
}

.activity-header{
    font-size: 2em;
}
.login-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    height: 80vh;
    width: 100%;
    margin:0 auto;
}

.login-welcome {
    margin-bottom: 2em;
    font-family: 'Poppins', sans-serif;
    font-size: 32px;
    font-weight: 300;
    letter-spacing: .1em;
    color: var(--darkest-mint);
}

.login-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    height: 40%;
    width: 50%;

    background-color: var(--main-white);
    border: 1px solid var(--main-blue);
    border-radius: 15px;
    box-shadow: 0px 0px 5px var(--dark-blue);
}

.login-form {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    height: 70%;
    width: 100%;;
}

.login-legend {
    font-size: 24px;
    letter-spacing: 0.05em;
    color: var(--main-blue);
}

.login-alt {
    padding-top: 1.5em;
    font-size: 1.6em;

    text-decoration: underline;
    color: var(--darkest-blue);

    border-top: 1px solid var(--darkest-blue);
}

.login-input,
.login-button {
    height: 15%;
    width: 70%;
    text-align: center;
    font-size: 20px;
    color: var(--dark-blue);

    border: 1px solid var(--main-blue);
    border-radius: 10px;
}

.login-input:hover {
    box-shadow: 0px 0px 5px var(--dark-accent);
}

.login-input:active,
.login-input:focus,
.login-button:active,
.login-button:focus {
    outline: none;
    border: 2px solid var(--main-accent);
}

.login-button {
    color: var(--main-white);
    background-color: var(--main-blue);
}

.error {
    font-size: 2em;
    font-weight: 500;
    color: #CE494A;
}

.chat-main-container {
    display: grid;
    grid-template-columns: 25% 70%;
    grid-column-gap: 5%;

    height: 80vh;
    width: 100%;

    padding: 1%;
    overflow: hidden;
}

.conversations {
    display: grid;
    grid-template-rows: 7.5% auto 10%;

    height: 100%;

   border: 2px solid var(--dark-blue);
   border-radius: 1.5em;
   background-color: var(--main-white);
   overflow: hidden;
}

.conversations-list {
    overflow: auto;
}

.conversation{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 2fr 3fr;
    grid-row-gap: .5em;

    height: 8vh;
    width: 95%;
    margin: .4em auto;
    padding: .5em;
    border:1px solid var(--darkest-blue);
    border-radius: 1em;

    text-overflow: ellipsis;
    overflow: hidden;
}

.contact-name {
    grid-column: 1;
    grid-row: 1;
    align-self: baseline;
    justify-self: flex-start;

    font-size: 1.4em;
    font-weight: 500;
}

.msg-time {
    grid-column: 2;
    grid-row: 1;
    align-self: baseline;
    justify-self: flex-end;

    color: var(--alt-shadow);
    font-size: 1.2em;
    font-weight: 300;
}

.msg-preview {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;

    grid-column: 1/3;
    grid-row: 2;
    height: 90%;

    overflow: hidden;
    
}

.new-conversation-button {
    cursor: pointer;
    border: none;
    background-color: var(--darkest-blue);
}

.chat {
    display: grid;
    grid-template-rows: 7.5% auto 15%;

    height: 100%;

    border: 2px solid var(--dark-blue);
    border-radius: 1.5em;
    background-color: var(--main-white);
    overflow: hidden;
}

.chat-feed {
    display: grid;
    grid-template-columns: 1fr 1fr;
    overflow-y: scroll;
}

.msg-container {
    grid-column: 1/3;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1.5em auto;
    min-height: 6em;
    max-height: 7em;
    width: 45%;
    margin: .5em 1em;
    padding: .5em .8em;

    font-size: 1.4em;

    border: 1px solid var(--darkest-blue);
    border-radius: 1em;
    background-color: var(--main-gray);
}

.message-from {
    grid-column: 1;
    color: var(--alt-transparent);
    font-size: .8em;
}

.message-time {
    grid-column: 2;
    color: var(--alt-transparent);
    font-size: .8em;
}

.message {
    grid-column: 1/3;
    grid-row: 2;
}

.msg-input {
    display: flex;
    flex-direction: column;
    width: 95%;
    margin: 0 auto;
    border-top: 1px solid var(--darkest-blue);
}

.msg-textarea {
    margin: auto;
    padding: .4em;
    height: 70%;
    width: 50%;
    resize: none;
    border: 1px solid var(--darkest-blue);
    border-radius: .5em;
}

.msg-textarea:focus {
    outline: none;
}

.msg-submit {
    margin: auto;
    width: 30%;
    color: var(--main-white);
    font-weight: 500;
    letter-spacing: 0.1em;
    border: none;
    border-radius: 1em;
    background-color: var(--darkest-mint);
}

.sent {
    grid-column: 1/3;
    justify-self: flex-end;
    
    background-color: var(--lightest-blue);
}

.received {
    grid-column: 1/3;
    background-color: var(--lightest-mint);
}
.diagnose {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 4em auto auto 2em;
    grid-row-gap: .5em;
    min-height: 25em;
    max-height: 30em;
    width: 90%;

    margin: 2em auto;

    border: 1px solid var(--dark-blue);
    border-radius: 15px;
    background-color: var(--main-white);
    box-shadow: 0px 0px 2px var(--darkest-blue);
    overflow: hidden;
}

.diagnose-header {
    grid-column: 1/4;
    grid-row: 1;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 2em;
